import Table from '../../mo/models';
import TableCell from '../../mo/models/tableCell';

export default function traverseAffectedColCells(
  table: Table,
  targetColIndex: number,
  callback: (rowIndex: number, colIndex: number, cell: TableCell) => void,
) {
  for (let rowIndex = 0; rowIndex < table.nodes.length; rowIndex += 1) {
    const row = table.nodes[rowIndex];
    for (let colIndex = 0; colIndex < targetColIndex; colIndex += 1) {
      const cell = row.nodes[colIndex];
      if (cell) { // 判断cell是否存在
        const hidden = cell.data.hidden || false;
        const colSpan = cell.data.colSpan || 1;
        if (!hidden && colIndex + colSpan - 1 >= targetColIndex) {
          callback(rowIndex, colIndex, cell);
        }
      }
    }
  }
}
